<template>
    <div class="flex">
       <img src="/static/img/avatar3.png" class="avatar"/>
       <span class="bb"></span>
       <div ref="list" class="file-list"></div>
    </div>
    <StatusLayout ref="sl"/>
</template>

<script>
const   master    = "https://api.github.com/repos/Jquil/jquil.github.io/branches/master"
const   TYPE_BLOB = 'blob',
        TYPE_TREE = 'tree'
const   ATTR_LOAD = "load",    // true && false
        ATTR_TYPE = "type",    // blob && tree
        ATTR_URL  = "url",
        ATTR_PATH = "path"
var mCounter      = 0
import StatusLayout from '../components/StatusLayout.vue'
export default {
    name: "File",
    components:{
        StatusLayout
    },
    methods:{

        // 请求
        req(url,call){
            this.$http.get(url)
            .then(res => {
                call(res.data)
            })
            .catch(err => {
                this.$refs.sl.error = true
                console.log(err)
            })
        },

        // 获取Master URL
        getMasterURL(call){
            this.req(master,(data) => {
                let url = data.commit.commit.tree.url
                call(url)
            })
        },

        // 获取文件信息
        getFile(url,call){
            this.req(url,(data) => {
                call(data.tree)
            })
        },

        // 监听点击
        listenClick(){
            let items = document.getElementsByName("item")
            let size  = items.length
            let _this = this
            for(let i = 0; i < size; i++){
                items[i].onclick = function(){
                    mCounter += 1
                    if(mCounter > 1)
                        return
                    setTimeout(() => {
                        mCounter = 0
                    },100)
                    switch(this.getAttribute(ATTR_TYPE)){
                        case TYPE_BLOB:
                            window.open(this.getAttribute(ATTR_PATH))
                            break;
                        case TYPE_TREE:
                            _this.handleDirReq(this)
                            break;
                    }
                }
            }
        },

        // 渲染
        render(el,data){
            el.setAttribute(ATTR_LOAD,true)
            let ul   = document.createElement("ul")
            let path = el.getAttribute(ATTR_PATH)
            data.forEach(item => {
                ul.innerHTML += `<li ${ATTR_LOAD}='false' ${ATTR_TYPE}='${item.type}' ${ATTR_URL}='${item.url}' ${ATTR_PATH}='${path}/${item.path}' name='item'>${item.path}</li>`
            })
            el.appendChild(ul)
            this.listenClick()
        },

        // 初始化
        init(el){
            this.$refs.sl.loading = true
            this.getMasterURL((url) => {
                this.getFile(url,(data) => {
                    this.render(el,data)
                    this.$refs.sl.loading = false
                })
            })
        },

        // 处理“文件夹请求”
        handleDirReq(obj){
            if(obj.getAttribute(ATTR_LOAD) == "true"){
                let display = obj.childNodes[1].style.display
                obj.childNodes[1].style.display = display == "" ? "none" : ""
            }
            else{
                this.getFile(obj.getAttribute(ATTR_URL),(data)=>{
                    this.render(obj,data)
                    obj.setAttribute(ATTR_LOAD,"true")
                })
            }
        }
    },


    mounted(){
         var list = this.$refs.list
         list.setAttribute(ATTR_PATH,"https://jqwong.cn")
         this.init(list)
    }
}
</script>


<style>
.file-list ul{
    width: 100%;
}
.file-list li{
    padding: 10px;
    margin: 5px;
    cursor: pointer;
}
</style>

<style scoped>
.avatar{
    width: 50%;
    margin: 0 auto;
    margin-top: 15px;
}
</style>